.btn {
  background-color: #fff;
  color: #e80368;
  width: auto;
  font-weight: 700;
  border: 1px solid #000;
}

.card {
  height: auto;
  width: 50%;
  position: fixed;
  box-shadow: 0 0 20px #000000;
  transition: all 0.5s;
  -webkit-animation: myCard 0.5s ease-in-out;
          animation: myCard 0.5s ease-in-out;
  border-radius: 10px;
  padding: 20px;
  top: 40%;
  left: 40%;
}
@media (max-width: 576px) {
  .card {
    left: 0;
    width: 100%;
  }
}
.card:hover {
  box-shadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.25),
    -5px -5px 20px 1px rgba(0, 0, 0, 0.22);
  transform: scale(0.9, 0.9);
}
@-webkit-keyframes myCard {
  from {
    opacity: 0;
    transform: translateX(-50%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}
@keyframes myCard {
  from {
    opacity: 0;
    transform: translateX(-50%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

.error {
  color: red;
  font-weight: 800;
  font-size: 20px;
}
.saveBtn {
  position: fixed;
  z-index: 1;
  top: 20px;
  right: 5%;
}
@media (max-width: 576px) {
  .saveBtn {
    top: 13%;
    left: 60%;
  }
}
input {
  width: 100%;
}

.form {
  position: relative;
  border-radius: 15px;
  background-color: #fff;
}

.myButton {
  display: inline;
  position: relative;
  left: 5px;
  cursor: pointer;
}
.form-control {
  width: 90%;
  display: inline;
  outline: none;
  border: none;
}
.form-control:focus {
  box-shadow: none;
}
@media (max-width: 576px) {
  .form-control {
    width: 70%;
  }
}

.bars {
  cursor: pointer;
  background-color: #e80368;
}

.header {
  background-color: #000;
  opacity: 0.9;
  height: 80px;
  top: 0;
  width: 100%;
  z-index: 0;
}

@media (max-width: 576px) {
  .header {
    height: 140px;
  }
}
.loadBtn {
  position: fixed;
  z-index: 1;
  top: 20px;
  right: 16%;
}
.loadBtn:disabled {
  background-color: transparent;
}
@media (max-width: 576px) {
  .loadBtn {
    top: 10%;
    left: 20%;
  }
}
h2 {
  color: #fff;
}

.sidebar {
  display: flex;
  flex: 1 1;
  position: relative;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: fixed;
  opacity: 0.8;
  z-index: 998;
  bottom: 0;
  top: 0;
  display: none;
}
@media (max-width: 576px) {
  .backdrop {
    display: block;
  }
}

.myList {
  padding: 5px;
  background-color: #e80368;
  height: 55px;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}
.message {
  font-size: 20px;
  color: #e80368;
  font-weight: 800;
  text-align: center;
  margin-top: 50%;
}
.myList:hover {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
  font-size: 18px;
}
#link {
  text-decoration: none;
}
.loadBtn {
  position: fixed;
  z-index: 1;
  top: 20px;
  right: 16%;
}
.cardBody p {
  color: #0000ff;
}
input {
  width: 100%;
  border: 5px solid #000;
}
@media (max-width: 576px) {
  .loadBtn {
    top: 13%;
    left: 20%;
  }
  .myList:hover {
    font-size: 18px;
  }
}

.dack {
  position: fixed;
  top: 80px;
  left: 0;
  width: 30%;
  height: 100%;
  background-color: #f2f3f5;
  transition: all 1s;
  overflow: scroll;
  z-index: 0;
}
@media (max-width: 576px) {
  .dack {
    top: 0;
    z-index: 999;
    width: 70%;
    -webkit-animation: sidebar 0.5s ease-in-out;
            animation: sidebar 0.5s ease-in-out;
  }
}
@-webkit-keyframes sidebar {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes sidebar {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}

